import LoginMask from './loginMask'
import QRCode from 'qrcodejs2'
const TIME_COUNT = 60;
export default {
  name: 'My',
  data() {
    return {
      formData:{
        phone:'',
        validataCode:'',
        messageCode:''
      },
      phone:'',
      showLogin: false,
      count: '获取验证码',
      timer: null,
      imgSrc:'http://test.hdjincheng.cn/wofinance/servlet/validateCodeServlet',
      isLogin:false,
      showCode:false,
      showPrompt:false,
      promptMsg:'',
      codeUrl:'',
      isHide:false
    }
  },
  created(){
    if(localStorage.getItem("userMessage")){
      this.phone = JSON.parse(localStorage.getItem("userMessage")).mobile
      this.isLogin =true
    }
    if(sessionStorage.getItem('bk')==1){
      this.isHide = true
    }
  },
  mounted(){
    this.qrcode()
  },
  methods: {
    listenClose(val){
      this.showLogin = val
    },
    qrcode () {
      let qrcode = new QRCode('qrcode', {
        width: 150,
        height: 150, // 高度
        text: '56663159' // 二维码内容
        // render: 'canvas' // 设置渲染方式（有两种方式 table和canvas，默认是canvas）
        // background: '#f0f'
        // foreground: '#ff0'
      })
    },
    isShowCode(){
      if(this.isLogin){

        this.$axios.post('/open/api/rcdRrcode/get',{rcdMobile:this.phone})
          .then(res=>{
            console.log(res)
            if(res.retCode=='0000'){
              this.showCode = true;
              this.codeUrl = res.data;
            }else {
              this.showPrompt = true;
              this.promptMsg = res.retMsg
            }
          })
      }else {
        this.showLogin = true
      }

    },
    toLogin(){
      if(this.GLOBAL.isKDApp){
        window.aladdin.navigator.forward({url:'http://test.hdjincheng.cn/#/login'});
      }else{
        this.$router.push("/login")
      }

    },
    toRegist(){
      if(this.GLOBAL.isKDApp){
        window.aladdin.navigator.forward({url:'http://test.hdjincheng.cn/#/regist'});
      }else{
        this.$router.push("./regist")
      }
    },
    toOrder() {
      if(this.GLOBAL.isKDApp){
        window.aladdin.navigator.forward({url:'http://test.hdjincheng.cn/#/order'});
      }else{
        this.$router.push("/order")
      }
    },
    toCustomOrder() {
      if(this.GLOBAL.isKDApp){
        window.aladdin.navigator.forward({url:'http://test.hdjincheng.cn/#/customOrder'});
      }else{
        this.$router.push("/customOrder")
      }
    },
    toChangePwd(){
      if(this.GLOBAL.isKDApp){
        window.aladdin.navigator.forward({url:'http://test.hdjincheng.cn/#/changePwd'});
      }else{
        this.$router.push("/changePwd")
      }
    },
    toCommission(){
      if(this.GLOBAL.isKDApp){
        window.aladdin.navigator.forward({url:'http://test.hdjincheng.cn/#/commission'});
      }else{
        this.$router.push("/commission")
      }
    },
    signOut(){
      this.phone = ""
      localStorage.clear();
      this.isLogin = false
      this.$store.commit('LOGOUT')

    },
    login(){
      // this.validateCode()
      this.$axios.post("/open/api/customer/save",{mobile:this.formData.phone})
        .then(res=>{
          localStorage.setItem("phone",this.formData.phone)
          this.phone = this.formData.phone;
          this.isLogin = true;
          this.showLogin = false;
        })
    },
    //点击图片重新获取验证码
    imgClick(){
      this.imgSrc = "http://test.hdjincheng.cn/wofinance/servlet/validateCodeServlet?"+Math.random();
    },
    //验证图形验证码
    validateCode(){
      this.$axios.get("/servlet/validateCodeServlet",{params:{validateCode:this.formData.validataCode}})
        .then(res=>{
          console.log(res)
        })
    },
    //获取验证码
    getCode(){
      if (!this.timer) {
        this.count = TIME_COUNT;
        this.timer = setInterval(() => {
          if (this.count > 0 && this.count <= TIME_COUNT) {
            this.count--;
          } else {
            this.count = "获取验证码";
            clearInterval(this.timer);
            this.timer = null;
          }
        }, 1000)
      }
    }
  },
  components: {
    LoginMask
  },
  watch:{
    '$store.state.login.isLogin'(cur){
      location.reload()
    }
  }
}



