

import Vue from 'vue'
import App from './App'
import router from './router'
import qs from "qs"
import store from "./store"

import Toast from 'vux/src/components/toast/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'

Vue.component('toast',Toast);
Vue.component('xDialog',XDialog);

Vue.config.productionTip = false;

import axios from 'axios'
axios.defaults.baseURL = "http://test.hdjincheng.cn/wofinance";
axios.defaults.headers['Content-Type'] = 'application/x-www-form-urlencoded';
Vue.prototype.$axios= axios;

import global_ from './components/Global'
Vue.prototype.GLOBAL = global_


Vue.filter('formatDate', function (value) {
let time = new Date(value);
let y = time.getFullYear();
let m = time.getMonth() + 1;
let d = time.getDate();
let h = time.getHours();
let mm = time.getMinutes();
let s = time.getSeconds();
return y+"-"+m+"-"+d+" "+h+":"+mm+":"+s
})


router.beforeEach((to, from, next) => {
next()
});


axios.interceptors.request.use(
config => {
if (localStorage.getItem('token')) {  // 判断是否存在token，如果存在的话，则每个http header都加上token
config.headers.Authorization = `Bearer ${localStorage.getItem('token')}`;
config.headers.token = localStorage.getItem('token');
config.headers.sessionid = localStorage.getItem('sessionid');
config.headers.tester = 1;
}
if((/\/open\/api\/order\/save/).test(config.url)){

return config;
}else {
config.data=qs.stringify(config.data)
return config;
}
},
err => {
return Promise.reject(err);
});


axios.interceptors.response.use(
response => {
let {status,data} = response;
if(status == 200) {

response.data = data.retObject;
if(data.retCode=='1006'){
router.replace({
path: '/regist',
query: {redirect: router.currentRoute.fullPath}
})
}
}
response.retMsg = data.retMsg;
response.retCode = data.retCode;
return response;
},
error => {
return Promise.reject('系统异常')   
});

/* eslint-disable no-new */
new Vue({
el: '#app',
router,
store,
components: { App },
template: '<App/>'
})



