
import Swiper from 'vux/src/components/swiper/swiper.vue'

import LoginMask from './loginMask'
export default {
name: 'Home',
data() {
return {
aspectRatio:1.146,
list: [],

swiperList: [{img: 'http://test.hdjincheng.cn/res/images/prod/8e12dd25b11543a18fec7bc5930f6d6b.png'}],
showDots: false,
descMask: false,
pageNo:1,
pageSize:10,
more:true,
isLogin:false
}
},
created() {
if(this.GLOBAL.isKDApp){
aladdin.header.config({

backgroundColor: '#ffffff',

underlineVisible: false,

leftVisible: true,

rightVisible: true,

middle: [{

type: 'text',

title: '中国联通',

textColor: '#f37937',

fontSize: 18,

backgroundColor: '#ffffff',

click: function () {

}
}],

left: [{









click: function () {
window.aladdin.navigator.back();
}
}],

right: [{






}]}, function (err, param) {

});
}
var url = location.href
function formatUrl(url){
var reg=/(?:[?&]+)([^&]+)=([^&#]+)/g; 
var data={};
function fn(str,pro,value){
data[decodeURIComponent(pro)]=decodeURIComponent(value);
}
url.replace(reg,fn);
return data;
}
var data=formatUrl(url);
this.getData()
if(data.key){
localStorage.setItem('key',data.key)
}else{
localStorage.removeItem('key')
}
if(data.bk==1){
sessionStorage.setItem('bk',1)
this.$store.commit('BK')
}
if(data.ext){
localStorage.setItem('ext',data.ext)

}else {
localStorage.setItem('ext',0)
localStorage.setItem('key','384af3af0dd948cfbeda2290146fa835')
}
if(data.cid){
localStorage.setItem('bankMsg',JSON.stringify(data))
}else {
let dataPrams={
source:'sa0000595',
outerSource:'os0003354',
outerid:'ou0000686',
cid:'ci0000001',
rec_no:'ZHANGLEIBJ'
}

localStorage.setItem('bankMsg',JSON.stringify(dataPrams))
}
this.getSwiper()
},
methods: {
getSwiper(){
this.$axios.post("/open/api/index/swiper/list")
.then(res=>{
for(let i=0;i<res.data.length;i++){
res.data[i].img = res.data[i].url
}
this.swiperList = res.data
})
},
listenClose(val){
this.isLogin = val
},
toDetail(id) {
this.$store.commit("PHONE",'请选择')
localStorage.removeItem('selectPhone');
if(this.GLOBAL.isKDApp){
window.aladdin.navigator.forward({url:'http://test.hdjincheng.cn/#/homeDetail?id='+id+'&reload='+new Date().getTime()});
}else {
this.$router.push({path:"/homeDetail",query:{id:id,reload:new Date().getTime()}})
}
},
getData(){
this.$axios.post("/open/api/product/list",{sellFlag:1,areaId: 2,pageNo:this.pageNo,pageSize:this.pageSize})
.then(res=>{
for(let i=0;i<res.data.list.length;i++){
if(!res.data.list[i].swiperList||res.data.list[i].swiperList.length==0){
res.data.list[i].swiperList=[{url:''}]
}
if(!res.data.list[i].packageList){
res.data.list[i].packageList=[{price:'',circle:''}]
}
}
this.list = res.data.list;
if(this.pageNo==res.data.totalPage){
this.more = false
}else {
this.pageNo+=1;
}
})
},
loadMore(){
this.$axios.post("/open/api/product/list",{sellFlag:1,areaId: 2,pageNo:this.pageNo,pageSize:this.pageSize})
.then(res=>{
for(let i=0;i<res.data.list.length;i++){
if(!res.data.list[i].swiperList||res.data.list[i].swiperList.length==0){
res.data.list[i].swiperList=[{url:''}]
}
if(!res.data.list[i].packageList){
res.data.list[i].packageList=[{price:'',circle:''}]
}
}
this.list = this.list.concat(res.data.list)
if(this.pageNo==res.data.totalPage){
this.more = false
}else {
this.pageNo+=1;
}
})
}
},
components: {
Swiper,
LoginMask
}
}



