
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Popup from 'vux/src/components/popup/index.vue'

export default {
name: 'commisson',
data () {
return {
tab:1,
showPrompt:false,
promptMsg:'',
totalPrice:0,
incomeData:[]
}
},
created(){
if(this.GLOBAL.isKDApp){
aladdin.header.config({

backgroundColor: '#ffffff',

underlineVisible: false,

leftVisible: true,

rightVisible: true,

middle: [{

type: 'text',

title: '中国联通',

textColor: '#f37937',

fontSize: 18,

backgroundColor: '#ffffff',

click: function () {

}
}],

left: [{









click: function () {
window.aladdin.navigator.back();
}
}],

right: [{






}]}, function (err, param) {

});
}
this.getIncome()
this.getPrice()
},
methods:{
onItemClick(index){
this.tab = index
},
getPrice(){
this.$axios.post('/open/api/rcdcash/balance',{mobile:JSON.parse(localStorage.getItem('userMessage')).mobile})
.then(res=>{
this.totalPrice = res.data
})
},
getIncome(){
this.$axios.post('/open/api/rcdcash/list',{mobile:JSON.parse(localStorage.getItem('userMessage')).mobile})
.then(res=>{
this.incomeData = res.data.list
})
},
tixian(){
this.showPrompt = true;
this.promptMsg = '暂未开放'
}
},
components: {
Tab,
TabItem
},
watch:{

}
}



