  import LoginMask from './components/loginMask'
export default {
  name: 'App',
  data(){
    return {
      title:'中国联通',
      keepAlive:'',
      isLogin:false,
      isBank:false
    }
  },
  created(){
    console.log(this.GLOBAL.isKDApp)
    if(sessionStorage.getItem('bk')==1&&!localStorage.getItem('userMessage')){
      this.isBank = true
    }
  },
  methods: {
    listenClose(val){
      this.isLogin = val
    },
    back(){
      this.$router.go(-1)
    }
  },
  watch:{
    $route:function (cur) {
      this.title = cur.meta.title
      this.keepAlive = cur.meta.keepAlive
    }
  },
  components: {
    LoginMask
  }
}



