
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


const TIME_COUNT = 60;
export default {
name: 'HomeDetail',
data() {
return {
hideBlur: false,
/*formData:{
phone:'',
password:''
},*/
formData: {
phone: '',
validataCode: '',
messageCode: ''
},
id: this.$route.query.id,
productData: {
packageList: [{name: ''}]
},
isMask: false,
aspectRatio: 1,
swiperList: [{img: ''}],
showAddress: false,
showBank: false,
showTips: false,
showLogin: false,
showFormat: false,
addressList: [],
address: {name: '请选择'},
colorList: ["深空灰", "金色", "玫瑰金"],
color: '请选择',
warnText: false,
errMsg: '',
showPrompt: false,
promptMsg: '',
count: '获取验证码',
timer: null,
stock: "",
selectPhone: '请选择',
phoneReg: /^1[3|4|5|7|8][0-9]{9}$/,
imgMsg: {
img: ''
},
messCode: '',
isImgCode: false,
rcdMobile: '',
phone: ''
}
},
created() {
if (this.GLOBAL.isKDApp) {
aladdin.header.config({

backgroundColor: '#ffffff',

underlineVisible: false,

leftVisible: true,

rightVisible: true,

middle: [{

type: 'text',

title: '详情',

textColor: '#f37937',

fontSize: 18,

backgroundColor: '#ffffff',

click: function () {

}
}],

left: [{









click: function () {
window.aladdin.navigator.back();
}
}],

right: [{






}]
}, function (err, param) {

});
}
window.scrollTo(0, 0)
this.init();
},
methods: {
init() {
window.scrollTo(0, 0)
if (localStorage.getItem('color')) {
this.color = localStorage.getItem('color')
localStorage.removeItem('color')
}
this.getData();
if (localStorage.getItem('key')) {
this.$axios.post('/open/api/rcdUser/get', {qrcodeKey: localStorage.getItem('key')})
.then(res => {
this.rcdMobile = res.data.rcdMobile;
})
}
if (localStorage.getItem('selectPhone')) {
this.phone = localStorage.getItem('selectPhone')
} else {
this.phone = '请选择'
}
},
oldUserFn(num) {
this.$axios.post("/open/busi/netQueryParams", {netMode: num})
.then(res => {
function standardPost(url, args) {
var form = document.createElement('form');
form.method = "post";
form.action = url;
for (var arg in args) {
var input = document.createElement('input');
input.name = arg;
input.value = args[arg];
form.appendChild(input)
}
document.getElementsByTagName('html')[0].appendChild(form)
form.submit();
}
standardPost('http://60.10.25.233:443/jkzyApp/woJXController/index.shtml', {msg:res.data})
})
},
toSelectPhone() {
localStorage.setItem('color', this.color);
if (this.GLOBAL.isKDApp) {
window.aladdin.navigator.forward({url: 'http://test.hdjincheng.cn/#/selectPhone?id=' + this.$route.query.id});
} else {
this.$router.push('/selectPhone?id=' + this.$route.query.id)
}
},

getData() {
this.$axios.post("/open/api/product/get", {id: this.$route.query.id})
.then(res => {
this.productData = res.data;

var arr = []
for (let i = 0; i < res.data.swiperList.length; i++) {
arr.push({img: res.data.swiperList[i].url})
}
this.swiperList = arr;
this.stock = res.data.amount;
/*if(this.productData.content){
document.getElementById("content").innerHTML = this.productData.content
}
if(this.productData.configDetail){
document.getElementById("content1").innerHTML = this.productData.configDetail
}*/
})
.catch(err => {
this.errMsg = err
this.warnText = true
})
},

openAddress() {
var params = {pid: 1}
this.$axios.post("/open/api/area/list", params)
.then(res => {
this.addressList = res.data;
this.showAddress = true;
})
.catch(err => {
this.errMsg = err
this.warnText = true
})
},

openFormat() {
this.showFormat = true;
},
selectAddress(address) {
this.address = address;
this.showAddress = false;
},
selectColor(color) {
this.color = color.color;
this.stock = color.amount
},
selectBank() {

if (this.address.name == "请选择") {
this.$axios.post("/open/api/area/list", {areaId: this.address.id})
.then(res => {
this.showBank = true;
this.bankList = res.data;
})
.catch(err => {
this.errMsg = err
this.warnText = true
})
} else {
this.showPrompt = true;
this.promptMsg = '请先选择区域'
}

},
confirm() {
let formData = {
addressName: this.address.name,

addressId: 2,
color: this.color,
}
if (!formData.addressId || this.phone == "请选择" || formData.color == '请选择') {
this.showPrompt = true;
this.promptMsg = "请补全信息"
} else {
this.showTips = true;
}
},
confirmTips() {
if (localStorage.getItem("userMessage")) {
let formData = {
addressName: "北京",

addressId: 2,
productName: this.productData.name,
setMealPrice: this.productData.price,
setMealName: this.productData.name,
circle: this.productData.circle,
price: this.productData.price,
busiType: this.productData.busiType,
color: this.color,
memory: this.productData.memory,
deposit: this.productData.deposit,
phone: this.phone,
id: this.$route.query.id
}
this.showTips = false;
if (this.GLOBAL.isKDApp) {
window.aladdin.navigator.forward({url: 'http://test.hdjincheng.cn/#/shopInfor?addressName=北京&addressId=2&productName=' + this.productData.name + '&setMealPrice=' + this.productData.price + '&setMealName=' + this.productData.name + '&circle=' + this.productData.circle + '&price=' + this.productData.price + '&busiType=' + this.productData.busiType + '&color=' + this.color + '&memory=' + this.productData.memory + '&deposit=' + this.productData.deposit + '&phone=' + this.phone + '&id=' + this.$route.query.id});
} else {
this.$router.push({path: "/shopInfor", query: formData})
}
} else {
this.showTips = false;
this.showLogin = true;
this.imgClick()
}
},
confirmFormat() {
if (this.stock == 0) {
this.showPrompt = true;
this.promptMsg = "暂无库存，请选择其他颜色或内存"
} else {
this.showFormat = false;
}
},
toBuy() {
if (!this.phoneReg.test(this.formData.phone)) {
this.showPrompt = true;
this.promptMsg = '请输入正确的手机号'
} else if (this.formData.messageCode == '') {
this.showPrompt = true;
this.promptMsg = '请输入验证码'
} else {
this.$axios.get("/open/validate/verify", {params: {code: this.formData.validataCode, key: this.imgMsg.key}})
.then(res => {
if (res.retCode != "0000") {
this.showPrompt = true;
this.promptMsg = res.retMsg;
this.isImgCode = false;
} else {
this.isImgCode = true;
if (!this.isImgCode) {
this.showPrompt = true;
this.promptMsg = '图形验证码不正确'
} else {
let params = {
mobile: this.formData.phone,
code: this.formData.messageCode
}
if (sessionStorage.getItem('bk') == 1) {
this.url = '/open/oauth/rcdLogin'
} else {
this.url = '/open/oauth/smsLogin'
}
this.$axios.post("/open/oauth/smsLogin", params)
.then(res => {
if (res.retCode == '0000') {
localStorage.setItem('userMessage', JSON.stringify(res.data.customer));
localStorage.setItem('token', JSON.stringify(res.data.token));
localStorage.setItem('sessionid', JSON.stringify(res.data.sessionid));
this.$store.commit('LOGIN', res.data.token);
this.showLogin = false;
let formData = {
addressName: "北京",

addressId: 2,
productName: this.productData.name,
setMealPrice: this.productData.price,
setMealName: this.productData.name,
circle: this.productData.circle,
price: this.productData.price,
busiType: this.productData.busiType,
color: this.color,
memory: this.productData.memory,
deposit: this.productData.deposit,
phone: this.phone,
id: this.$route.query.id
}
this.showTips = false;
if (this.GLOBAL.isKDApp) {
window.aladdin.navigator.forward({url: 'http://test.hdjincheng.cn/#/shopInfor?addressName=北京&addressId=2&productName=' + this.productData.name + '&setMealPrice=' + this.productData.price + '&setMealName=' + this.productData.name + '&circle=' + this.productData.circle + '&price=' + this.productData.price + '&busiType=' + this.productData.busiType + '&color=' + this.color + '&memory=' + this.productData.memory + '&deposit=' + this.productData.deposit + '&phone=' + this.phone + '&id=' + this.$route.query.id});
} else {
this.$router.push({path: "/shopInfor", query: formData})
}
/*this.$axios.post("/open/api/customer/save",{mobile:this.formData.phone})
.then(res=>{
localStorage.setItem("phone",this.formData.phone)
this.phone = this.formData.phone;
this.showLogin = false;
let formData = {
addressName:"北京",

addressId:2,
productName:this.productData.name,
setMealPrice:this.productData.packageList[0].price,
setMealName:this.productData.packageList[0].name,
circle:this.productData.packageList[0].circle,
price:this.productData.price,
busiType:this.productData.busiType,
color:this.color,
memory:this.productData.memory,
deposit:this.productData.deposit,
phone:this.phone
}
this.showTips = false
this.$router.push({path:"/shopInfor",query:formData})
})*/
} else {
this.showPrompt = true;
this.promptMsg = res.retMsg
}
})
.catch(err => {
this.showPrompt = true;
this.promptMsg = '系统异常'
})
}
}
})
}
},

getCode() {
this.$axios.post("/open/oauth/smscode", {mobile: this.formData.phone})
.then(res => {
if (res.retCode == '0000') {
this.messCode = res.data;
if (!this.timer) {
this.count = TIME_COUNT;
this.timer = setInterval(() => {
if (this.count > 0 && this.count <= TIME_COUNT) {
this.count--;
} else {
this.count = "获取验证码";
clearInterval(this.timer);
this.timer = null;
}
}, 1000)
}
}
})
},
login() {
if (!this.phoneReg.test(this.formData.phone)) {
this.showPrompt = true;
this.promptMsg = '请输入正确的手机号'
} else if (this.formData.messageCode == '') {
this.showPrompt = true;
this.promptMsg = '请输入验证码'
} else {
this.$axios.get("/open/validate/verify", {params: {code: this.formData.validataCode, key: this.imgMsg.key}})
.then(res => {
if (res.retCode != "0000") {
this.showPrompt = true;
this.promptMsg = res.retMsg;
this.isImgCode = false;
} else {
this.isImgCode = true;
if (!this.isImgCode) {
this.showPrompt = true;
this.promptMsg = '图形验证码不正确'
} else {
let params = {
mobile: this.formData.phone,
code: this.formData.messageCode
}
this.$axios.post("/open/api/customer/save", params)
.then(res => {
if (res.retCode == '0000') {


this.$router.go(-1)
} else {
this.showPrompt = true;
this.promptMsg = res.retMsg
}
})
.catch(err => {
this.showPrompt = true;
this.promptMsg = '系统异常'
})
}
}
})
}

},

imgClick() {
this.$axios.post("/open/validate/gcode")
.then(res => {
if (res.retCode == "0000") {
this.imgMsg = res.data
}
})
},

validateCode() {
this.$axios.get("/open/validate/verify", {params: {code: this.formData.validataCode, key: this.imgMsg.key}})
.then(res => {
if (res.retCode != "0000") {
this.showPrompt = true;
this.promptMsg = res.retMsg;
this.isImgCode = false;
} else {
this.isImgCode = true;
}
})
}
},
components: {
Swiper,
Popup
},
watch: {
'$route.query.reload': function (v) {
console.log(v)
if (v && !this._inactive) this.init()
}
}
}



