  import LoginMask from './loginMask'
export default {
  name: 'Order',
  data () {
    return {
      formData:{
        phone:'',
        validataCode:'',
        messageCode:''
      },
      showLogin: false,
      count: '获取验证码',
      timer: null,
      imgSrc:'http://test.hdjincheng.cn/wofinance/servlet/validateCodeServlet',
      orderList:[],
      isLogin:false
    }
  },
  created(){
    if(this.GLOBAL.isKDApp){
      aladdin.header.config({
        //导航头部背景颜色
        backgroundColor: '#ffffff',
        //是否显示导航头部底部线条
        underlineVisible: false,
        //是否显示左区域按钮
        leftVisible: true,
        //是否显示右区域按钮
        rightVisible: true,
        //导航头部中间区域，
        middle: [{
          //类型（text、image、search等）
          type: 'text',
          //标题 页面title，自定义
          title: '客户订单',
          //文字颜色
          textColor: '#f37937',
          //文字大小
          fontSize: 18,
          //背景颜色
          backgroundColor: '#ffffff',
          //回调事件
          click: function () {
            //do something
          }
        }],
        //左区域
        left: [{
          //图标
          //icon: '/navBar/images/navBar/scan@2x.png',
          //图标颜色
          //tintColor: '#999999',
          //背景颜色
          //backgroundColor: '#ffffff',
          //是否显示小红点
          //badge: false,
          //回调事件
          click: function () {
            window.aladdin.navigator.back();
          }
        }],
        //右区域
        right: [{
          //图标
          //icon: '/navBar/images/navBar/customer@2x.png',
          //回调事件
          //click: function () {
          //do something
          //}
        }]}, function (err, param) {
        //设置导航栏回调
      });
    }
    //若未登陆弹出登陆框
    if(localStorage.getItem("userMessage")){
      this.getData()
    }else {
      this.isLogin = true
    }
  },
  methods: {
    listenClose(val){
      this.isLogin = val
    },
    login(){
      // this.validateCode()
      this.$axios.post("/open/api/customer/save",{mobile:this.formData.phone})
        .then(res=>{
          localStorage.setItem("phone",this.formData.phone)
          this.phone = this.formData.phone;
          this.showLogin = false;
          this.getData()
        })
    },
    //点击图片重新获取验证码
    imgClick(){
      this.imgSrc = "http://test.hdjincheng.cn/wofinance/servlet/validateCodeServlet?"+Math.random();
    },
    //验证图形验证码
    validateCode(){
      this.$axios.get("/servlet/validateCodeServlet",{params:{validateCode:this.formData.validataCode}})
        .then(res=>{
          console.log(res.data)
        })
    },
    //获取验证码
    getCode() {
      if (!this.timer) {
        this.count = TIME_COUNT;
        this.timer = setInterval(() => {
          if (this.count > 0 && this.count <= TIME_COUNT) {
            this.count--;
          } else {
            this.count = "获取验证码";
            clearInterval(this.timer);
            this.timer = null;
          }
        }, 1000)
      }
    },
    //获取订单列表
    getData(){
      this.$axios.post("/open/api/order/list",{rcdMobile:JSON.parse(localStorage.getItem("userMessage")).mobile})
        .then(res=>{
          for(let i=0;i<res.data.list.length;i++){
            if(!res.data.list[i].swiperList||res.data.list[i].swiperList.length==0){
              res.data.list[i].swiperList=[{url:''}]
            }
          }
          this.orderList = res.data.list;
        })
        .catch(error => {

        })
    },
    toDetail(id){
      if(this.GLOBAL.isKDApp){
        window.aladdin.navigator.forward({url:'http://test.hdjincheng.cn/#/orderDetail?id='+id+'&type=1'});
      }else{
        this.$router.push({path:'/orderDetail',query:{id:id,type:1}})
      }
    }
  },
  components: {
    LoginMask
  },
  watch:{
    '$store.state.login.isLogin'(cur){
      this.getData()
    }
  }
}



