export default {
  name: 'OrderDetail',
  data() {
    return {
      orderMsg:{},
      isPay:false
    }
  },
  created() {
    if(this.GLOBAL.isKDApp){
      aladdin.header.config({
        //导航头部背景颜色
        backgroundColor: '#ffffff',
        //是否显示导航头部底部线条
        underlineVisible: false,
        //是否显示左区域按钮
        leftVisible: true,
        //是否显示右区域按钮
        rightVisible: true,
        //导航头部中间区域，
        middle: [{
          //类型（text、image、search等）
          type: 'text',
          //标题 页面title，自定义
          title: '订单详情',
          //文字颜色
          textColor: '#f37937',
          //文字大小
          fontSize: 18,
          //背景颜色
          backgroundColor: '#ffffff',
          //回调事件
          click: function () {
            //do something
          }
        }],
        //左区域
        left: [{
          //图标
          //icon: '/navBar/images/navBar/scan@2x.png',
          //图标颜色
          //tintColor: '#999999',
          //背景颜色
          //backgroundColor: '#ffffff',
          //是否显示小红点
          //badge: false,
          //回调事件
          click: function () {
            window.aladdin.navigator.back();
          }
        }],
        //右区域
        right: [{
          //图标
          //icon: '/navBar/images/navBar/customer@2x.png',
          //回调事件
          //click: function () {
          //do something
          //}
        }]}, function (err, param) {
        //设置导航栏回调
      });
    }
    this.$axios.post("/open/api/order/detail/get", {id: this.$route.query.id})
      .then(res => {
          if(!res.data.swiperList){
            res.data.swiperList=[{url:''}]
          }
        this.orderMsg = res.data;
        if(this.orderMsg.payStatus=='待支付'&&(this.$route.query.type==2)){
          this.isPay = true
        }
      })
  },
  methods:{
    submit(){
      let koudai;
      if(this.GLOBAL.isKDApp){
        koudai=1;
      }else {
        koudai=0;
      }
      this.$axios.post("/open/api/aotoLoginURL",{orderNo:this.orderMsg.orderNo,koudai:koudai})
        .then(res=>{
          window.location.href=res.data;
        })
      // this.$router.push("/writePhone")
    }
  }
}



